import React, { useState } from "react";
import ListGroup from "react-bootstrap/ListGroup";
import InputGroup from "react-bootstrap/InputGroup";
import { Container, FormControl } from "react-bootstrap";
const drugs = [
  {
    name: "Acetaminophen",
    slug: "acetaminophen",
  },
  {
    name: "Bufferin",
    slug: "bufferin",
  },
  {
    name: "Clorazil",
    slug: "clorazil",
  },
  {
    name: "Diltiaz",
    slug: "diltiaz",
  },
  {
    name: "Foscavir",
    slug: "foscavir",
  },
  {
    name: "Exforge",
    slug: "exforge",
  },
].sort((a, b) => {
  if (a.name < b.name) {
    return -1;
  }
  if (a.name > b.name) {
    return 1;
  }
  return 0;
});

const DrugsList = () => {
  const [search, setSearch] = useState("");
  const changeSearch = (e) => {
    setSearch(e.target.value);
    renderList();
  };
  const renderList = () => {
    if (search) {
      return drugs
        .filter((drug) => drug.name.toLowerCase().includes(search.toLowerCase()))
        .map((drug) => {
          return <ListGroup.Item as="li">{drug.name}</ListGroup.Item>;
        });
    } else {
      return drugs.map((drug) => {
        return <ListGroup.Item as="li">{drug.name}</ListGroup.Item>;
      });
    }
  };

  return (
    <>
      <Container>
        <InputGroup className="mb-3">
          <FormControl
            placeholder="Search"
            aria-label="Search"
            aria-describedby="basic-addon1"
            value={search}
            onChange={changeSearch}
          />
        </InputGroup>
        <ListGroup as="ul">{renderList()}</ListGroup>
      </Container>
    </>
  );
};

export default DrugsList;
