import React from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import NavBar from "./components/NavBar";
import Home from "./pages/Home";
import DrugsList from "./pages/drugs/DrugsList";
import InteractionChecker from "./pages/checker/InteractionChecker";
import DiseaseList from "./pages/diseases/DiseasesList";
import PillIdentifier from "./pages/identifier/PillIdentifier";
import HealthProfessional from "./pages/professionals/HealthProfessional";
import HealthProtocol from "./pages/protocols/HealthProtocol";

function App() {
  return (
    <Router>
      <NavBar />
      <Switch>
        <Route path="/" exact>
          <Home />
        </Route>
        <Route path="/drugs" exact>
          <DrugsList />
        </Route>
        <Route path="/interaction-checker" exact>
          <InteractionChecker />
        </Route>
        <Route path="/diseases-conditions" exact>
          <DiseaseList />
        </Route>
        <Route path="/pill-identifier" exact>
          <PillIdentifier />
        </Route>
        <Route path="/health-professionals" exact>
          <HealthProfessional />
        </Route>
        <Route path="/health-protocols" exact>
          <HealthProtocol />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
