import React,{useState} from "react";
import InputGroup from "react-bootstrap/InputGroup";
import { Container, FormControl, Row } from "react-bootstrap";
import Protocol from "../../components/Protocol";
import emergency from '../../assets/images/Standard Treatment Protocol of emergency Health Service.png'

const HealthProtocol = () => {
  const protocols = [
    {
      name: "Standard Treatment Protocol of emergency Health Service",
      img: emergency,
    },
    {
      name: "Test",
      img: emergency,
    },
  ].sort((a, b) => {
    if (a.name < b.name) {
      return -1;
    }
    if (a.name > b.name) {
      return 1;
    }
    return 0;
  });
  const [search, setSearch] = useState("");
  const changeSearch = (e) => {
    setSearch(e.target.value);
    renderList();
  };
  const renderList = () => {
    if (search) {
      return protocols
        .filter((protocol) =>
          protocol.name.toLowerCase().includes(search.toLowerCase())
        )
        .map((protocol) => {
          return (
            <Protocol name={protocol.name} img={protocol.img} path="/" />
          );
        });
    } else {
      return protocols.map((protocol) => {
        return <Protocol name={protocol.name} img={protocol.img} path="/" />;
      });
    }
  };
  return (
    <>
      <Container>
        <InputGroup className="mb-3">
          <FormControl
            placeholder="Search"
            aria-label="Search"
            aria-describedby="basic-addon1"
            value={search}
            onChange={changeSearch}
          />
        </InputGroup>
        <Row>{renderList()}</Row>
      </Container>
    </>
  );
};

export default HealthProtocol;
