import React from 'react'
import Card from "react-bootstrap/Card";
import {Col} from 'react-bootstrap'
import { Link } from "react-router-dom";

const Protocol = ({img,name,path}) => {
    return (
      <Col md={4} sm={6} lg={3} xs={12} style={{ marginBottom: "5px" }}>
        <Link to={path}>
          <Card>
            <Card.Body>
              <Card.Text style={{ textAlign: "center" }}>
                <Card.Img
                  variant="top"
                  src={img}
                  style={{ width: "100%" }}
                />
              </Card.Text>
              <Card.Text style={{ textAlign: "center" }}>
                {name}
              </Card.Text>
            </Card.Body>
          </Card>
        </Link>
      </Col>
    );
}
export default Protocol;